.projects {
    background-color: var(--overlay);
    color: #fff;
    height: 100vh;
    width: 100%;
    position: relative;
}


.projects .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

h2 a {
    color: black; /* Default color */
    text-decoration: none; /* Remove underline */
  }

  h2 a:hover {
    color: orange; /* Color on hover */
  }
  